<template>
  <div>
    <!-- Modal -->
    <div class="modal fade" ref="modal" tabindex="-1" role="dialog">
      <div class="modal-dialog modal-dialog-centered modal-lg" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">
              Information
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            TODO: Completar con texto en ingles
          </div>
          <div class="modal-footer">
            <div class="form-check">
              <input
                class="form-check-input"
                type="checkbox"
                v-model="noMostrarNuevamente"
                id="defaultCheck1"
              />
              <label class="form-check-label" for="defaultCheck1">
                Do not show this message again.
              </label>
            </div>

            <button
              type="button"
              class="btn btn-secondary"
              data-dismiss="modal"
            >
              OK
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- Fin Modal -->
  </div>
</template>

<script>
const MENSAJE_INICIAL = "mensaje_inicial";
//const MINUTOS_DIFF_MAX = 5;

// function sameDay(d1, d2) {
//   return (
//     d1.getFullYear() === d2.getFullYear() &&
//     d1.getMonth() === d2.getMonth() &&
//     d1.getDate() === d2.getDate()
//   );
// }

// function diff_minutes(dt2, dt1) {
//   var diff = (dt2.getTime() - dt1.getTime()) / 1000;
//   diff /= 60;
//   return Math.abs(Math.round(diff));
// }

export default {
  data() {
    const noMostrarNuevamente = false;
    return {
      noMostrarNuevamente,
    };
  },
  mounted() {
    $(this.$refs.modal).on("hidden.bs.modal", () => {
      this.cerroModal();
    });

    this.showInitialMessage();
  },
  watch: {
    ["$i18n.locale"]() {
      this.showInitialMessage();
    },
  },
  methods: {
    showInitialMessage() {
      const showModal = () => $(this.$refs.modal).modal("show");

      const canShowMessage = () => {
        const isLocaleEn = this.$i18n.locale === "en";
        if (!isLocaleEn) return false;

        let mensajeInicial;
        try {
          mensajeInicial = JSON.parse(localStorage.getItem(MENSAJE_INICIAL));
        } catch (e) {
          mensajeInicial = null;
        }
        if (!mensajeInicial) return true;

        return !mensajeInicial.noMostrarNuevamente;

        // const minutosDiff = diff_minutes(new Date(), mensajeInicial.ultVez);
        // if (minutosDiff > MINUTOS_DIFF_MAX) return true;
      };

      if (canShowMessage()) showModal();
    },
    cerroModal() {
      console.log(
        "Cerro Modal",
        "noMostrarNuevamente: ",
        this.noMostrarNuevamente
      );

      localStorage.setItem(
        MENSAJE_INICIAL,
        JSON.stringify({
          noMostrarNuevamente: this.noMostrarNuevamente,
          ultVez: new Date(),
        })
      );
    },
  },
};
</script>

